import React, {PureComponent} from 'react';
import Label from 'r-cmui/components/Label';
import Table from 'r-cmui/components/Table';
import Row from 'r-cmui/components/Row';
import Col from 'r-cmui/components/Col';

import Demo1 from './demo1';
import Demo2 from './demo2';
import Demo3 from './demo3';
import Demo4 from './demo4';
import Demo5 from './demo5';
import Demo6 from './demo6';
import Demo7 from './demo7';



class Page extends PureComponent{
    render(){
        return (
            <div className="main-container">
                <h1 className="page-h1">Progress </h1>
                <blockquote className="page-tip">
                    <br/>
                    
                </blockquote>

                <h1 className="page-h1"></h1>

                <Row>
                    <Col grid={0.5} className="code-col">
                        <Label className="code-box">
                            <Demo1></Demo1>
                        </Label>
                        <Label className="code-box">
                            <Demo3></Demo3>
                        </Label>
                        <Label className="code-box">
                            <Demo5></Demo5>
                        </Label>
                        <Label className="code-box">
                            <Demo7></Demo7>
                        </Label>
                    </Col>
                    <Col grid={0.5} className="code-col">
                        <Label className="code-box">
                            <Demo2></Demo2>
                        </Label>
                        <Label className="code-box">
                            <Demo4></Demo4>
                        </Label>
                        <Label className="code-box">
                            <Demo6></Demo6>
                        </Label>
                    </Col>
                </Row>

                <h2 className="page-h2">API</h2>
                <h3 className="page-h3">Progress</h3>

                <Table columns={[
                    {name: 'param', text: ''},
                    {name: 'desc', text: ''},
                    {name: 'type', text: ''},
                    {name: 'default', text: ''}
                ]} bordered={true} data={[
                    {param: 'className', desc: 'class', type: 'string', default: ''},
                    {param: 'style', desc: '', type: 'object', default: ''},
                    {param: 'value', desc: '', type: 'number', default: '0'},
                    {param: 'min', desc: '', type: 'number', default: '0'},
                    {param: 'max', desc: '', type: 'number', default: '100'},
                    {param: 'radius', desc: '', type: 'number', default: '60'},
                    {param: 'strokeWidth', desc: '', type: 'number', default: '10'},
                    {param: 'status', desc: '', type: 'string', default: ''},
                    {param: 'showPercent', desc: '', type: 'boolean', default: 'true'},
                    {param: 'active', desc: '', type: 'boolean', default: 'false'},
                    {param: 'type', desc: 'line circle', type: 'string', default: ''},
                    {param: 'format', desc: '', type: 'function', default: ''}
                ]}></Table>

                <h3 className="page-h3">Methods</h3>

                <ul className="code-methods">
                    <li>
                        <i>getMin()</i>
                        <span></span>
                        <ul>
                            <li>
                                return {`Number`} 
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>getMax()</i>
                        <span></span>
                        <ul>
                            <li>
                                return {`Number`} 
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>getValue()</i>
                        <span></span>
                        <ul>
                            <li>
                                return {`Number`} 
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>update(value)</i>
                        <span></span>
                        <ul>
                            <li>
                                value {`Number`}
                            </li>
                        </ul>
                    </li>
                </ul>
            </div>
        );
    }
}

export default Page;



